@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 2em;
    font-weight: 600;
    text-transform: uppercase;
    border-radius: 3.125em;
    height: 5.25em;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 1.125em;
        line-height: 150%;
    }
    svg{
        width: 1.5em;
        height: auto;
        margin-left: .625em;
    }
}

.m-btn-primary{
    background-color: $primary;
    color: #fff;
    &:hover{
        background-color: darken($primary,10%);
        color: #fff;
    }
}

.m-btn-primary-outline{
    border: 1px solid $primary;
    color: #fff;
    &._white{
        &:not(:hover){
            border-color: #fff;
        }
    }
    &:hover{
        background-color: $primary;
        color: #fff;
    }
}


@media screen and (max-width: 992px){
    .m-btn{
        height: 3.75em;
        span{
            font-size: .875em;
        }
        svg{
            margin-left: 1.875em;
        }
    }
}
