.infrastructure{
  padding: 3.75em 0;
  position: relative;
  &__rect{
    position: absolute;
    width: 54.6875em;
    display: block;
    left: 0;
    top: -47.875em;
    z-index: -1;
  }
  &__top{
    margin: 0 auto;
    width: 61em;
    max-width: 100%;
    text-align: center;
  }
  &__text{
    margin-top: 1.25em;
    @include grid100gap(.5em);
  }
  &__grid{
    margin-top: 3.125em;
    display: grid;
    grid-template-columns: calc(100% - 40.25em) 35.25em;
    justify-content: space-between;
  }
  &__left{
    max-width: 66.5em;
    padding-top: 8.44em;
  }
  &__img{
    margin-top: 3.125em;
    img{
      width: 100%;
      display: block;
      border-radius: 3.125em;
    }
  }
  &__items{
    padding-left: 4.06em;
    @include grid100gap(1.69em);
    position: relative;
    &:before{
      content: '';
      position: absolute;
      height: 100%;
      width: 4.6875em;
      background-image: url(../img/connect.svg);
      @include bgImgSize(100%);
      transform: translateY(-1.5em);
    }
  }
}

.awards{
  display: flex;
  justify-content: center;
  &__item{
    height: 7.5em;
    flex-shrink: 0;
    @include lastM(0 1.25em 0 0);
    img{
      height: 100%;
      width: auto;
      display: block;
    }
  }
}

.infrastructure-card{
  padding: 1.875em;
  background-color: $lightGray;
  border-radius: .75em;
  &._blue{
    color: #fff;
    background-color: $primary;
  }
  &__icon{
    height: 3.125em;
    margin-bottom: 1.25em;
    img{
      height: 100%;
      width: auto;
      display: block;
    }
  }
  .text24{
    margin-bottom: .42em;
  }
}

.infrastructure2{
  padding: 3.75em 0;
  position: relative;
  &__rect{
    top: -63.9375em;
    width: 100%;
    left: 0;
    z-index: -1;
    position: absolute;
  }
  .text60{
    text-align: center;
  }
  &__grid{
    margin-top: 3.125em;
    display: grid;
    grid-template-columns: repeat(4,calc(25% - .9375em));
    gap: 1.25em;
  }
}

.infrastructure-card2{
  border-radius: 1.25rem;
  background: #FFF;
  box-shadow: 0 0 0 0 rgba(204, 207, 245, 0.10), 0 1.1875em 2.625em 0 rgba(204, 207, 245, 0.10), 0 4.75em 4.75em 0 rgba(204, 207, 245, 0.09), 0 10.6875em 6.4375em 0  rgba(204, 207, 245, 0.05), 0 19em 7.625em 0 rgba(204, 207, 245, 0.01), 0 29.75em 8.3125em 0 rgba(204, 207, 245, 0.00);
  padding: 1.875em;
  &__icon{
    height: 5.3125em;
    margin-bottom: 1.25em;
    @include center;
    img{
      width: auto;
      height: 100%;
      display: block;
    }
  }
  &__title{
    text-align: center;
    margin-bottom: 3.25em;
    min-height: 4.125em;
  }
  &__list{
    @include grid100gap(1.25em);
    li{
      display: flex;
      font-size: 1em;
      line-height: 150%;
      &:before{
        content: '';
        flex-shrink: 0;
        width: 0.5em;
        height: 0.5em;
        border-radius: 50%;
        background: $gradient;
        margin: .625em .625em 0 0;
      }
    }
  }
}

.how-manage{
  padding: 3.75em 0;
  &__desc{
    margin-top: 2.75em;
    max-width: 62.5em;
  }
  .slider{
    margin-top: 3.5em;
    position: relative;
  }
  .swiper{
    &-container{
      overflow: hidden;
    }
    &-nav{
      position: absolute;
      right: 0;
      bottom: 100%;
      margin-bottom: 3.125em;
      display: flex;
    }
    &-button{
      position: static;
      margin: 0 0 0 .3125em;
      width: 4em;
      height: 4em;
      border-radius: 50%;
      background: $gradient;
      img{
        width: 1.5em;
      }
    }
    &-pagination{
      display: none;
    }
  }
  &__img{
    img{
      border-radius: 1.25em;
      width: 100%;
      display: block;
    }
  }
}

.why{
  padding: 3.75em 0;
  position: relative;
  &__rect{
    width: 100%;
    position: absolute;
    left: 0;
    top: -67em;
  }
  ._rect2{
    display: none;
  }
  &__grid{
    position: relative;
    z-index: 2;
    display: flex;
    flex-wrap: wrap;
    width: calc(100% + 1.25em);
    margin-top: -1.25em;
  }
  &__title{
    width: calc(50% - 1.25em);
    margin: 1.25em 1.25em 0 0;
    color: #fff;
  }
}

.why-card{
  width: calc(25% - 1.25em);
  margin: 1.25em 1.25em 0 0;
  padding: 3.125em 1.875em;
  border-radius: .625em;
  background-color: $lightGray;
  &__icon{
    height: 5.3125em;
    margin-bottom: 1.875em;
    img{
      height: 100%;
      width: auto;
      display: block;
    }
  }
}

.cases{
  padding: 3.75em 0;
  position: relative;
  &__rect{
    position: absolute;
    left: 0;
    z-index: -1;
    top: -17em;
    width: 100%;
    display: block;
  }
}

.cases-slider{
  position: relative;
  z-index: 2;
  border-radius: 1.25em;
  background: #FFF;
  box-shadow: 0px 0px 0px 0px rgba(204, 207, 245, 0.10), 0px 19px 42px 0px rgba(204, 207, 245, 0.10), 0px 76px 76px 0px rgba(204, 207, 245, 0.09), 0px 171px 103px 0px rgba(204, 207, 245, 0.05), 0px 304px 122px 0px rgba(204, 207, 245, 0.01), 0px 476px 133px 0px rgba(204, 207, 245, 0.00);
  margin-top: 3.125em;
  .swiper{
    &-pagination{
      display: none;
    }
    &-nav{
      position: absolute;
      right: 31.81em;
      bottom: 7.8em;
      display: flex;
    }
    &-button{
      position: static;
      margin: 0 0 0 .3125em;
      width: 4em;
      height: 4em;
      border-radius: 50%;
      border: 1px solid $primary;
      svg{
        width: 1.5em;
        height: auto;
      }
      &:hover{
        background-color: $primary;
        path{
          fill: #fff;
        }
      }
    }
  }
  &__inner{
    display: grid;
    grid-template-columns: calc(100% - 45em) 40em;
    justify-content: space-between;
    padding-left: 6.25em;
  }
  &__content{
    padding: 7.8em 0;
    max-width: 46.875em;
  }
  &__co{
    @include grid100gap(.5em);
  }
  &__info{
    margin-top: 3.125em;
    @include grid100gap(1.25em);
    &-item {
      @include grid100gap(.5em);
    }
  }
  &__nav{
    display: flex;
    margin-top: 4.625em;
  }
  &__btn{
    height: 4em;
    padding: 0 4.78em;
  }
  &__img{
    margin-right: -5em;
    width: calc(100% + 5em);
    filter: drop-shadow(0px 0px 0px rgba(204, 207, 245, 0.10))
            drop-shadow(0px 19px 42px rgba(204, 207, 245, 0.10))
            drop-shadow(0px 76px 76px rgba(204, 207, 245, 0.09))
            drop-shadow(0px 171px 103px rgba(204, 207, 245, 0.05))
            drop-shadow(0px 304px 122px rgba(204, 207, 245, 0.01))
            drop-shadow(0px 476px 133px rgba(204, 207, 245, 0.00));
    img{
      width: 100%;
      display: block;
    }
  }
}

.learn-more{
  padding: 3.75em 0;
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.5em));
    gap: 3em;
  }
  &__img{
    img{
      width: 100%;
      display: block;
      border-radius: 3.125em;
    }
  }
  &__text{
    @include grid100gap(1.25em);
  }
  &__items{
    max-width: 26em;
    margin-top: 3.125em;
    @include grid100gap(1.875em);
    li{
      display: flex;
      font-size: 1em;
      line-height: 150%;
      &:before{
        content: '';
        flex-shrink: 0;
        width: 1.5em;
        height: 1.5em;
        margin-right: 1.25em;
        background-image: url(../img/check2.svg);
        @include bgImgSize(100%);
      }
    }
  }
}

.more-content{
  padding: 3.75em 0;
  &__text{
    text-align: center;
    @include grid100gap(1.25em);
  }
  &__links{
    max-width: 61.25em;
    margin: 3.125em auto 0;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .625em));
    gap: 1.25em;
  }
  &__link{
    display: flex;
    align-items: center;
    height: 6.875em;
    padding: 1.875em;
    border-radius: 0.625em;
    background-color: #fff;
    box-shadow: 0px 4px 80px 0px rgba(123, 140, 172, 0.25), 0px 0px 0px 0px rgba(204, 207, 245, 0.10), 0px 19px 42px 0px rgba(204, 207, 245, 0.10), 0px 76px 76px 0px rgba(204, 207, 245, 0.09), 0px 171px 103px 0px rgba(204, 207, 245, 0.05), 0px 304px 122px 0px rgba(204, 207, 245, 0.01), 0px 476px 133px 0px rgba(204, 207, 245, 0.00);
    &:hover{
      background-color: rgba(#fff,.3);
    }
    img{
      width: 3.125em;
      flex-shrink: 0;
      margin-right: 1.25em;
    }
    span{
      font-size: 1.25em;
      line-height: 130%;
    }
  }
}

.consult{
  padding: 3.75em 0;
  &__inner{
    background-image: url(../img/consult.svg);
    background-size: cover;
    background-position: center;
    padding: 6.75em 2em 7.25em;
    text-align: center;
    border-radius: 12.5em 12.5em 1.25em 1.25em;
    color: #fff;
    position: relative;
  }
  .text60{
    text-align: center;
  }
  .rect1{
    width: 11.28525rem;
    height: 11.28525rem;
    left: 6.9em;
    bottom: -4.68em;
  }
  .rect2{
    width: 14.90625rem;
    height: 14.90625rem;
    right: 3.375em;
    top: -4.5em;
  }
}

.consult-form{
  margin: 3.125em auto 0;
  width: 56.5em;
  max-width: 100%;
  display: grid;
  grid-template-columns: repeat(3,calc(100% / 3 - 2.5em / 3));
  gap: 1.25em;
  ._full{
    grid-column: 1/-1;
  }
  &__nav{
    display: flex;
    justify-content: center;
  }
  &__btn{
    height: 4em;
    width: 21.5625em;
    max-width: 100%;
  }
}

.faq{
  padding: 3.75em 0;
  .text60{
    text-align: center;
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .625em));
    gap: .625em 1.25em;
    margin-top: 3.125em;
  }
}

.qa-card{
  @include lastM(0 0 .625em);
  &.slideToggle-block--active{
    .qa-card__btn{
      svg{
        transform: rotate(-45deg);
      }
    }
  }
  &__show{
    box-shadow: 0 0 0 0 rgba(123,140,172,.25),0 0 0 0 rgba(204,207,245,.1),0 3px 5px 0 rgba(204,207,245,.1),0 8px 8px 0 rgba(204,207,245,.09),0 8px 5px 0 rgba(204,207,245,.05),0 25px 10px 0 rgba(204,207,245,.01),0 40px 20px 0 rgba(204,207,245,0);
    background-color: #fff;
    border-radius: 31.25em;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1.875em;
  }
  &__btn{
    width: 1.5em;
    flex-shrink: 0;
    margin-left: .625em;
    &:hover{
      path{
        fill: $primary;
      }
    }
    svg{
      width: 100%;
      height: auto;
    }
  }
  .slideToggle-block__wrap{
    padding: 1em 1.875em;
  }
}


@media screen and (max-width: 992px){
  .infrastructure{
    padding: 1.625em 0;
    &__rect{
      display: none;
    }
    &__grid{
      grid-template-columns: 100%;
      margin-top: 1.29em;
      gap: 1.25em;
    }
    &__left{
      padding-top: 0;
    }
    &__text{
      gap: .625em;
    }
    &__img{
      margin-top: 1.25em;
      img{
        border-radius: 1.25em;
      }
    }
  }

  .infrastructure-card{
    .text24{
      font-size: 1.5em;
    }
  }

  .infrastructure2{
    padding: 1.625em 0;
    &__rect{
      display: none;
    }
    &__grid{
      margin-top: 1.25em;
      grid-template-columns: repeat(2,calc(50% - .625em));
    }
  }

  .infrastructure-card2{
    &__title{
      margin-bottom: 2.5em;
    }
  }

  .how-manage{
    padding: 1.625em 0;
    .text60{
      text-align: center;
    }
    &__desc{
      text-align: center;
      margin-top: 1.25em;
    }
    .slider{
      margin-top: 1.25em;
    }
    .swiper{
      &-nav{
        display: none;
      }
      &-pagination{
        display: flex;
      }
    }
    &__img{
      img{
        border-radius: 0.3125em;
      }
    }
  }

  .why{
    padding: 1.5625em 0;
    background: $gradient;
    &__rect{
      top: -10em;
    }
    ._rect1{
      display: none;
    }
    &__grid{
      width: calc(100% + .625em);
      margin-top: -.625em;
    }
    &__title{
      width: calc(100% - .625em);
      text-align: center;
      margin: .625em .625em .625em 0;
    }
  }

  .why-card{
    width: calc(50% - .625em);
    margin: .625em .625em 0 0;
    padding: 1.875em;
    &__icon{
      height: 3.125em;
      margin-bottom: 1.25em;
    }
    .text22{
      font-size: 1em;
      line-height: 150%;
    }
  }

  .cases{
    padding: 1.625em 0;
    overflow: hidden;
    &__rect{
      display: none;
    }
    .text60{
      text-align: center;
    }
  }

  .cases-slider{
    padding: 0;
    margin-top: 10em;
    .swiper{
      &-nav{
        display: none;
      }
      &-pagination{
        display: flex;
        transform: translateY(1.25em);
      }
    }
    &__inner{
      padding: 0 1.875em 1.375em;
      grid-template-columns: 100%;
    }
    &__img{
      width: calc(100% + 3.75em);
      margin: -10em 0 0 -1.875em;
      order: -1;
      padding: 0 1em;
    }
    &__content{
      padding: 0;
    }
    &__co{
      gap: 1.25em;
      &-item{
        @include grid100gap(.25em);
      }
    }
    &__info{
      display: none;
    }
    &__nav{
      margin-top: 1.25em;
    }
    &__btn{
      padding: 0;
      width: 100%;
      svg{
        display: none;
      }
    }
  }

  .learn-more {
    padding: 1.625em 0;
    &__grid {
      grid-template-columns: 100%;
      gap: 1.25em;
    }
    &__content{
      order: -1;
    }
    &__items{
      margin-top: 1.25em;
      gap: 1.25em;
    }
    &__img{
      img{
        border-radius: .625em;
      }
    }
  }

  .more-content{
    padding: 1.625em 0;
    &__links{
      margin-top: 1.25em;
    }
  }

  .consult{
    padding: 1.625em 0;
    .container{
      padding: 0;
    }
    .rect{
      display: none;
    }
    &__inner{
      background-image: url(../img/consult2.svg);
      border-radius: 1.25em 1.25em 3.125em 3.125em;
      padding: 2.88em 1.25em;
    }
  }

  .consult-form{
    margin-top: 1.25em;
    grid-template-columns: 100%;
    gap: .625em;
  }

  .faq{
    padding: 1.625em 0;
    &__grid{
      margin-top: 1.25em;
      grid-template-columns: 100%;
    }
  }

  .qa-card{
    &__show{
      padding: 1.25em;
      border-radius: 0.625em;
    }
    .slideToggle-block__wrap{
      padding: .5em 1.25em;
      .text16{
        font-size: .75em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .infrastructure{
    &__img{
      @include imgCover(107%);
    }
    &__items{
      padding-left: 0;
      gap: .625em;
      &:before{
        display: none;
      }
    }
  }

  .awards{
    display: grid;
    grid-template-columns: 1fr 1fr .9fr;
    gap: 1em;
    &__item{
      height: auto;
      margin: 0 !important;
      img{
        width: 100%;
        height: auto;
      }
    }
  }

  .infrastructure2{
    z-index: 2;
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .infrastructure-card2{
    &__title{
      min-height: unset;
      margin-bottom: 2.5em;
    }
  }

  .why{
    background: unset;
    ._rect2{
      display: block;
      z-index: -1;
    }
  }

  .why-card{
    width: calc(100% - .625em);
    br{
      display: none;
    }
  }

  .more-content{
    &__links{
      grid-template-columns: 100%;
      gap: .625em;
    }
    &__link{
      padding-right: .5em;
    }
  }
}
