.fg {
  position: relative;
  margin: 0;
  textarea,
  input {
    border: none;
    padding: 0 1.875em;
    height: 4em;
    border-radius: 31.25em;
    width: 100%;
    font-size: 1em;
    background-color: #fff;
    color: #000;
    &::placeholder {
      color: $gray;
    }
  }
  textarea{
    border-radius: 1.25em;
    resize: none;
    padding: 1.875em;
    height: 12.5em;
  }
}

.m-switch{
  position: relative;
  input {
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
    &:checked+label{
      background: $gradient;
      &:before{
        background-color: #fff;
        box-shadow: 0 .125em .5em 0 rgba(0, 0, 0, 0.16);
        transform: translateX(1.5em);
      }
    }
  }
  label{
    width: 3em;
    height: 1.5em;
    border-radius: 1em;
    background-color: #D2D9ED;
    transition: all .3s;
    display: flex;
    align-items: center;
    padding: 0 .125em;
    &:before{
      content: '';
      flex-shrink: 0;
      width: 1.25em;
      height: 1.25em;
      background-color: #9EB1D7;
      border-radius: 50%;
      transition: all .3s;
    }
  }
}

.ccalc{
  width: 9.375em;
  height: 3.125em;
  position: relative;
  input{
    width: 100%;
    height: 100%;
    text-align: center;
    border-radius: 31.25em;
    border: 1px solid #D2D9ED;
    background: #FFF;
  }
  &__btn{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    width: 1em;
    @include center;
    svg{
      width: 100%;
      height: auto;
    }
    &:hover{
      path{
        fill: $primary;
      }
    }
  }
  ._minus{
    left: 1.25em;
  }
  ._plus{
    right: 1.25em;
  }
}

.check {
  position: relative;
  input {
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
    &:checked+label {
      &::before {
        // background-color: $green;
        border: 3px solid #fff;
      }
    }
    &:disabled+label {
      color: #AAAAAA;
      pointer-events: none;
    }
  }
  label {
    padding-left: 40px;
    position: relative;
    min-height: 20px;
    font-size: 14px;
    line-height: 20px;
    cursor: pointer;
    &:before {
      content: '';
      position: absolute;
      z-index: 1;
      border-radius: 2px;
      background: rgba(255, 255, 255, 0.01);
      border: 1px solid rgba(255, 255, 255, 0.45);
      width: 20px;
      height: 20px;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      transition: all .100s;
    }
  }
}
