.text60{
  font-size: 3.75em;
  line-height: 130%;
}

.text32{
  font-size: 2em;
  line-height: 130%;
}

.text24{
  font-size: 1.5em;
  line-height: 150%;
}

.text22{
  font-size: 1.375em;
  line-height: 150%;
}

.text20{
  font-size: 1.25em;
  line-height: 130%;
}

.text18{
  font-size: 1.125em;
  line-height: 150%;
}

.text16{
  font-size: 1em;
  line-height: 150%;
}

.text14{
  font-size: .875em;
  line-height: 130%;
}

.text12{
  font-size: .75em;
  line-height: 130%;
}

.bold-text{
  font-weight: 700;
}

.semi-bold-text{
  font-weight: 600;
}

.medium-text{
  font-weight: 500;
}

.gray-text{
  color: $gray;
}


@media screen and (max-width: 992px){
  .text60{
    font-size: 1.75em;
  }

  .text18-mob,
  .text32,
  .text24{
    font-size: 1.125em;
  }

  .text16-mob,
  .text22{
    font-size: 1em;
  }
}
