.main{
  padding: 5.2em 0 3.25em;
  position: relative;
  overflow: hidden;
  &:before{
    content: '';
    position: absolute;
    width: 93.75em;
    height: 93.75em;
    border-radius: 6.25rem;
    background: linear-gradient(135deg, #318DDF 31.77%, #64E6FA 100%), #328DE0;
    top: -24em;
    right: -53em;
    transform: rotate(-45deg);
    z-index: 2;
  }
  &__inner{
    background: url(../img/lines.svg), linear-gradient(135deg, #318DDF 31.77%, #4CBCED 80.21%, #64E6FA 100%);
    background-size: cover;
    padding: 5.44em 6.25em 5em;
    border-radius: 1.25em 1.25em 12.5em 12.5em;
    color: #fff;
  }
  &__top{
    width: calc(100% + 6.25em);
    position: relative;
    z-index: 3;
    display: grid;
    grid-template-columns: 56.5em 36.0625em;
    align-items: flex-start;
    justify-content: space-between;
  }
  &__info{
    max-width: 52em;
    @include grid100gap(1.25em);
    .text32{
      max-width: 24em;
    }
  }
  &__title{
    font-size: 4.375em;
    line-height: 130%;
    font-weight: 500;
  }
  &__nav{
    position: relative;
    z-index: 3;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .625em));
    gap: 1.25em;
    margin-top: 2.5em;
  }
  .rect1{
    top: 10.96em;
    left: 4.71em;
  }
  .rect2{
    width: 6.25em;
    height: 6.25em;
    left: 11.29em;
    top: 41.17em;
    border-radius: 1.25em;
  }
  .rect3{
    width: 3.125em;
    height: 3.125em;
    left: 48.08em;
    top: 40.08em;
  }
  .rect4{
    width: 6.25em;
    height: 6.25em;
    left: 69.42em;
    top:  31.92em;
    z-index: 3;
    border-radius: 1.25em;
  }
}

.main-rating{
  width: 100%;
  transform: translateX(2em);
  padding: 3.125em;
  border-radius: 1.25em;
  border: 1px dashed #67B1F2;
  backdrop-filter: blur(75px);
  &__items{
    margin-top: 1.25em;
    @include grid100gap(1.25em);
  }
  &__item{
    display: flex;
    align-items: center;
    &:before{
      content: '';
      width: 1em;
      height: 1em;
      background-image: url(../img/crown.svg);
      @include bgImgSize(100%);
      margin-right: 1.25em;
      flex-shrink: 0;
    }
  }
}

.advantages{
  position: relative;
  z-index: 2;
  display: grid;
  grid-template-columns: repeat(5,calc(20% - 1em));
  gap: 1.25em;
  margin-top: 7.5em;
}

.advantage-card{
  border-radius: .3125em;
  background: #3B9EE4;
  padding: 1.25em 1.56em;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: .625em;
  }
  &__icon{
    width: 1.5em;
  }
}

.rect{
  position: absolute;
  width: 3.125em;
  height: 3.125em;
  border-radius: 0.625em;
  background: linear-gradient(135deg, #318DDF 31.77%, #64E6FA 100%), #328DE0;
  box-shadow: 0 3.125em 6.25em 0 rgba(37, 121, 195, 0.50);
  transform: rotate(-45deg);
  pointer-events: none;
}

.can-locate{
  margin-top: 7.5em;
  position: relative;
  z-index: 1;
  @media screen and (min-width: 993px){
    &__top{
      position: absolute;
      top: 0;
      left: 0;
      width: calc(100% / 3 - 2.5em / 3);
    }
    .swiper{
      &-wrapper{
        display: grid;
        grid-template-columns: repeat(3,calc(100% / 3 - 2.5em / 3));
        gap: 1.25em;
        transform: unset !important;
        &:before{
          content: '';
        }
      }
      &-slide{
        width: 100% !important;
      }
      &-pagination{
        display: none;
      }
    }
  }
}

.locate-card{
  padding: 3.125em 1.875em;
  background-color: $lightGray;
  display: flex;
  align-items: flex-start;
  height: 100%;
  min-height: 16.4375em;
  &__icon{
    padding-top: .875em;
    flex-shrink: 0;
    width: 3.125em;
    margin-right: 1.875em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__content{
    @include grid100gap(.625em);
  }
}

.cloud{
  padding: 3.25em 0;
}

.how-start,
.conditions{
  z-index: 2;
  background-size: cover;
  background-position: center;
  color: #fff;
  position: relative;
  .rect{
    border-radius: 1.25em;
  }
  &__info{
    text-align: center;
    @include grid100gap(3.125em);
  }
}

.how-start{
  border-radius: 1.25em 1.25em 12.5em 12.5em;
  background-image: url(../img/how-work.svg);
  padding: 10.625em 10em;
  .rect1{
    left: 1.125em;
    bottom: 9.875em;
    width: 14.90625em;
    height: 14.90625em;
  }
  .rect2{
    left: 28.28em;
    top: 2em;
    width: 5.58419em;
    height: 5.58419em;
  }
  .rect3{
    right: 18.38em;
    bottom: 3.125em;
    width: 5.58419em;
    height: 5.58419em;
  }
  .rect4{
    right: -1.74em;
    top: 7.96em;
    width: 14.90625em;
    height: 14.90625em;
  }
  &__inner{
    width: 70em;
    max-width: 100%;
    margin: 0 auto;
  }
  &__nav{
    margin-top: 1.25em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .625em));
    gap: 1.25em;
  }
}

.conditions{
  margin-top: 7.5em;
  padding: 13.81em 0;
  background-image: url(../img/conditions.svg);
  border-radius: 12.5em 12.5em 1.25em 1.25em;
  &__nav{
    margin-top: 1.875em;
    display: flex;
    justify-content: center;
  }
  &__btn{
    padding: 0 3.75em;
  }
  .rect1{
    width: 14.84138em;
    height: 14.84138em;
    top: -1.3em;
    left: 10em;
  }
  .rect2{
    right: 10em;
    top: -2.78em;
    width: 5.58419em;
    height: 5.58419em;
  }
  .rect3{
    right: 19em;
    bottom: -2em;
    width: 6.98663em;
    height: 6.98663em;
  }
}

.calc{
  margin-top: 7.5em;
  position: relative;
  &__inner{
    margin-top: 4em;
    display: grid;
    grid-template-columns: calc(100% - 36.25em) 33.125em;
    gap: 3.125em;
    align-items: flex-start;
    position: relative;
    z-index: 2;
  }
  &__content{
    @include grid100gap(1.25em);
  }
  &__sidebar{
    color: #fff;
  }
  &__rect{
    position: absolute;
    right: -6.25em;
    top: -35em;
    width: 99.1875em;
  }
}

.calc-group{
  padding: 1.875em;
  border-radius: 0 1.25em 1.25em 1.25em;
  background: #FFF;
  box-shadow: 0 0 0 0 rgba(204, 207, 245, 0.10), 0 1.1875em 2.625em 0 rgba(204, 207, 245, 0.10), 0px 76px 76px 0px rgba(204, 207, 245, 0.09), 0px 171px 103px 0px rgba(204, 207, 245, 0.05), 0 19em 7.625em 0 rgba(204, 207, 245, 0.01), 0 29.75em 8.3125em 0 rgba(204, 207, 245, 0.00);
  &__items{
    margin-top: 1.875em;
    @include grid100gap(1.25em);
  }
}

.calc-item{
  padding-bottom: 1.25em;
  border-bottom: 1px solid #F2F5FC;
  &__grid{
    margin-top: 1.125em;
    display: grid;
    grid-template-columns: 11em calc(100% - 18.75em) 5em;
    gap: 1.875em;
    align-items: center;
  }
  &__info{
    @include grid100gap(.8125em);
  }
  &__top{
    display: flex;
    align-items: center;
    .hint-btn{
      margin-left: .625em;
      flex-shrink: 0;
    }
  }
  &__output{
    @include center;
    height: 3.125em;
    border: 1px solid #D2D9ED;
    border-radius: 31.25em;
    &-num{
      @include sameNumWidth;
    }
    &-measure{
      margin-left: .25em;
    }
  }
}

.range-slider{
  height: .1875em;
  border-radius: 3.125em;
  background: #E3E7F3;
  border: none;
  box-shadow: none;
  &__nums{
    width: calc(100% + .5em);
    margin-left: -.25em;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: .875em;
  }
}

.noUi-connect{
  background: $gradient;
}

.noUi-handle{
  box-shadow: none;
  border: none;
  width: 1.3125em !important;
  height: 1.3125em !important;
  border-radius: 50%;
  background: $gradient;
  right: -.625em !important;
  top: -.5625em !important;
  cursor: pointer;
  &:hover{
    transform: scale(1.1);
  }
  &:before,
  &:after{
    display: none;
  }
}

.backup{
  margin-top: 1.875em;
  @include grid100gap(1.875em);
  .calc-item{
    padding: 0 0 1.875em;
  }
}

.backup-group{
  display: grid;
  grid-template-columns: 3em calc(100% - 4.25em);
  gap: 1.25em;
  &__top{
    display: grid;
    grid-template-columns: 18em calc(100% - 19em);
    gap: 1em;
  }
  &__desc{
    display: flex;
    align-items: center;
    color: #000;
    .hint-btn{
      flex-shrink: 0;
      margin-right: .625em;
    }
  }
  &__text{
    margin-top: 1.25em;
  }
  .calc-item__grid{
    grid-template-columns: calc(100% - 6.875em) 5em;
  }
}

.virtual-machine{
  margin-top: 1.5em;
  .ccalc{
    margin-top: 1em;
  }
}

.calc-result{
  &__text{
    @include grid100gap(.625em);
  }
  &__items{
    margin-top: 3.125em;
    @include grid100gap(3.75em);
  }
  &__item{
    @include grid100gap(1.875em);
  }
}


@media screen and (max-width: 992px){
  .main{
    padding: 0 0 1.5em;
    &:before{
      display: none;
    }
    &__inner{
      padding: 0;
      border-radius: 0;
      background: unset;
    }
    &__top{
      z-index: 1;
      background-image: url(../img/lines2.svg);
      background-size: cover;
      margin-left: -1.25em;
      padding: 5em 1.25em 16.25em;
      width: calc(100% + 2.5em);
      grid-template-columns: calc(100% - 23.25em) 22em;
      gap: 3.125em 1.25em;
    }
    &__info{
      .text32{
        max-width: 100%;
      }
    }
    &__title{
      font-size: 2em;
    }
    &__nav{
      margin-top: 1.25em;
      gap: .625em;
      grid-template-columns: 100%;
    }
    .rect{
      display: none;
    }
  }

  .main-rating{
    padding: 1.875em;
    transform: unset;
  }

  .advantages{
    grid-template-columns: repeat(3,calc(100% / 3 - .625em / 3));
    gap: .625em;
    margin-top: -15em;
    z-index: 3;
  }

  .advantage-card{
    padding: 1.25em;
    .text22{
      font-size: 1.375em;
    }
  }

  .can-locate{
    margin-top: 3.125em;
    .slider{
      margin-top: 1.25em;
    }
    .swiper{
      &-container{
        width: calc(100% + 1em);
      }
      &-wrapper{
        align-items: stretch;
      }
      &-slide{
        width: 50%;
        padding-right: 1em;
        height: auto;
      }
      &-pagination{
        display: flex;
      }
    }
  }

  .locate-card{
    height: 100%;
    flex-direction: column;
    align-items: center;
    text-align: center;
    &__icon{
      height: 5.3125em;
      margin: 0 0 2.19em;
      img{
        width: auto;
        height: 100%;
        display: block;
      }
    }
  }

  .cloud{
    padding: 1.5em 0;
  }

  .calc-group,
  .calc__sidebar,
  .how-start,
  .conditions{
    padding: 3.125em 1.25em;
    width: calc(100% + 2.5em);
    margin-left: -1.25em;
    border-radius: 1.25em 1.25em 3.125em 3.125em;
    .rect{
      display: none;
    }
  }

  .how-start{
    &__info{
      gap: 1.25em;
      .text18{
        margin-top: 1.67em;
      }
    }
  }

  .conditions{
    display: flex;
    flex-direction: column;
    justify-content: center;
    min-height: 20em;
    margin-top: 3.125em;
    &__info{
      gap: 1.875em;
    }
    &__nav{
      margin-top: 1.25em;
    }
  }

  .calc{
    margin-top: 3.125em;
    &__inner{
      margin-top: 1.25em;
      grid-template-columns: 100%;
      gap: 0;
    }
    &__rect{
      display: none;
    }
    &__sidebar{
      border-radius: 0 0 3.125em 3.125em;
      background: $gradient;
      text-align: center;
    }
  }

  .calc-group{
    border-radius: 0;
    box-shadow: 0 0 3.125em 0 rgba(204, 207, 245, 0.50);
    &__title{
      text-align: center;
      font-size: 1.375em;
    }
    &__items{
      margin-top: 1.25em;
    }
  }

  .calc-item{
    padding-bottom: 0;
    border-bottom: none;
    .text18{
      font-size: .875em;
    }
    &__grid{
      margin-top: .3125em;
      grid-template-columns: 100%;
      gap: .625em;
    }
    &__top{
      .text22{
        font-size: 1.375em;
      }
    }
    &__range{
      padding: 0 .25em;
    }
  }

  .backup{
    gap: 1.25em;
  }

  .backup-group{
    &:not(:last-child){
      padding-bottom: 1.25em;
      border-bottom: 1px solid #F2F5FC;
    }
    &__top{
      grid-template-columns: 100%;
      gap: .3125em;
    }
    &__desc{
      .hint-btn{
        display: none;
      }
      .text14{
        font-size: .75em;
      }
    }
    &__text{
      margin-top: .625em;
    }
    .calc-item{
      padding: 0;
      width: calc(100% + 4.25em);
      margin-left: -4.25em;
      &__grid{
        grid-template-columns: 100%;
      }
    }
  }

  .calc-result{
    &__text{
      gap: 0;
    }
    .text60{
      font-size: 2.5em;
    }
    &__items{
      margin-top: 1.875em;
      gap: 1.25em;
    }
    &__item{
      gap: .625em;
    }
  }
}


@media screen and (max-width: 576px){
  .main{
    &__top{
      grid-template-columns: 100%;
      &:before{
        content: '';
        position: absolute;
        border-radius: 6.25em;
        background: linear-gradient(135deg, #318DDF 31.77%, #64E6FA 100%), #328DE0;
        width: 81.16813em;
        height: 81.16813em;
        top: 31em;
        left: -13em;
        transform: rotate(-45deg);
      }
    }
    &__info{
      text-align: center;
    }
    &__btn{
      text-align: left;
      span{
        max-width: 9em;
      }
    }
  }

  .advantages{
    grid-template-columns: 100%;
  }

  .can-locate{
    .swiper-slide{
      width: 100%;
    }
  }

  .how-start,
  .conditions{
    ._mob-center{
      text-align: left;
      span{
        max-width: 9em;
      }
    }
  }

  .how-start{
    background-image: url(../img/how-work2.svg);
    &__nav{
      grid-template-columns: 100%;
      gap: .625em;
    }
    ._hide-icon{
      svg{
        display: none;
      }
    }
  }

  .conditions{
    min-height: 40.25em;
    background-image: url(../img/conditions2.svg);
    &__btn{
      padding: 0 1em;
      width: 100%;
    }
  }

  .calc-result{
    &__btn{
      padding: 0 1.7em;
      svg{
        display: none;
      }
    }
  }
}
