.header{
  padding: 1.44em 0;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__logo{
    display: block;
    width: 11.25em;
    img{
      width: 100%;
      display: block;
    }
    ._view2{
      display: none;
    }
  }
  &__btn{
    font-weight: 500;
    color: #fff;
    margin-left: 2.56em;
    svg{
      display: none;
    }
    span{
      font-size: 1em;
      line-height: 130%;
    }
    &:hover{
      color: rgba(#fff,.75);
    }
  }
}

.burger{
  display: none;
}

.nav{
  display: flex;
  align-items: center;
  &__link{
    font-size: 1em;
    line-height: 130%;
    font-weight: 500;
    @include lastM(0 2.5em 0 0);
    &._white{
      color: #fff;
      &:hover{
        color: #fff;
        opacity: .75;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .header{
    padding: .875em 0;
    &__logo{
      width: 9em;
      z-index: 10;
      position: relative;
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
    &__buttons{
      display: flex;
      align-items: center;
    }
    &__btn{
      span{
        display: none;
      }
    }
    &._open{
      .burger{
        ._view1{
          opacity: 0;
        }
        ._view2{
          opacity: 1;
        }
      }
      .menu{
        transform: translateX(0%);
      }
    }
  }

  .menu{
    background-color: darken(#64E6FA,40%);
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    padding: 6em 1.25em 3em;
    transform: translateX(-100%);
    transition: all .3s;
  }

  .nav{
    flex-direction: column;
    align-items: flex-start;
    &__link{
      color: #fff;
      @include lastM(0 0 1.5em);
      &:hover{
        opacity: .75;
        color: #fff;
      }
    }
  }

  .header__btn,
  .burger{
    @include center;
    width: 2.75em;
    height: 2.75em;
    border-radius: 0.3125em;
    border: 1px solid $primary;
    position: relative;
    z-index: 10;
    margin-left: .625em;
    svg{
      @include centerAbsolute;
      display: block;
      width: 1.5em;
      height: auto;
    }
    ._view2{
      opacity: 0;
    }
  }
}
